.login {
  width: 100vw;
  height: 100vh;
  background: -webkit-linear-gradient(left, #4F85FC, #4EEDF6);
  background: linear-gradient(to right, #4F85FC, #4EEDF6);
}
.login .companyName {
  color: #fff;
  font-size: 2em;
}
.login .companyName .fa {
  margin-right: 1rem;
}
.login .login-form-button {
  width: 50%;
  display: block;
  box-shadow: none;
  border-color: #53AEF8;
  background: -webkit-linear-gradient(right, #48F8D3, #69CDF7);
  background: linear-gradient(to left, #48F8D3, #69CDF7);
}
.login .loginremark {
  color: #fff;
  position: fixed;
  right: 5px;
  bottom: 5px;
}
